<template>

  <div class="flex flex-col ">

    <div class="overflow-x-auto  sm:-mx-6 lg:-mx-8">
      <div class="overflow-hidden">
        <table class="min-w-full ">
          <!-- head -->
          <thead class="border-b bg-gray-900">
            <tr>
              <th scope="col" class="text-sm font-medium text-gray-100 px-6 py-4 text-center"> Id </th>
              <th scope="col" class="text-sm font-medium text-gray-100 px-6 py-4 text-center">Name</th>
              <th scope="col" class="text-sm font-medium text-gray-100 px-6 py-4 text-center">sec</th>
              <th scope="col" class="text-sm font-medium text-gray-100 px-6 py-4 text-right"><button class="btn btn-info btn-xs"><router-link to="/form">Register!</router-link></button></th>


            </tr>
          </thead>
          <tbody>
            <tr class="bg-gray-800 " v-for="student in Students" :key="student._id">

              <td class="text-sm font-medium text-gray-100 px-6 py-4 text-center" v-if="student.section === 2">
                {{student.id}}</td>
              <td class="text-sm font-medium text-gray-100 px-6 py-4 text-center" v-if="student.section === 2">
                {{student.name}}</td>
              <td class="text-sm font-medium text-gray-100 px-6 py-4 text-center" v-if="student.section === 2">
                {{student.section}}</td>
              <td class="text-sm font-medium text-gray-100 px-6 py-4 text-center" v-if="student.section === 2">
                <button class="btn btn-outline btn-error btn-xs"
                  @click.prevent="deleteStudent(student.id)">Delete</button>

              </td>



            </tr>


          </tbody>
        </table>
      </div>
    </div>
  </div>

</template>

<script>
import axios from 'axios';
import Swal from 'sweetalert2';
import ArtBord from './ui/ArtBord.vue';
export default {
data() {
  return {
    Students: [],
  };
},
created() {
  let apiURL = process.env.PORT+"/api" ;
  axios.get(apiURL).then(res => {
    this.Students = res.data;
    this.Students.sort();

    console.log(this.Students);
  }).catch(error => {
    console.log(error);
  });
},
methods: {
  deleteStudent(id) {
    let apiURL = process.env.PORT+"/api"+`/delete/${id}`;

    let indexOfArrayItem = this.Students.findIndex(i => i._id === id);
    const conf = window.prompt(`pls Input ${id} to confirm delete`);
    if (conf + "" === id + "") {
      if (window.confirm("Do you really want to delete?")) {
        {
          axios.delete(apiURL).then(() => {
            this.Students.splice(indexOfArrayItem, 1);
          }).catch(error => {
            console.log(error);
          });
        }
      }
    }
  },

},
components: { ArtBord }
}
</script>

