<template>
  <div>
    <Spinner v-if="isLoad"></Spinner>
    <layout-telephone v-if="!isLoad">
      <body>
        <br />

        <form @submit.prevent="saveData">
          <div class="form-control" style="border: none">
            <br />
            <img
              src="https://cdn.pixabay.com/photo/2020/12/09/21/11/jellyfish-5818601_960_720.jpg"
              class="imgJelly"
            />
            <div class="form-control w-full max-w-xs">
              <label class="label">
                <span class="label-text">Your Id : </span>
              </label>
              <input
                type="number"
                placeholder="Type here"
                class="input w-full max-w-xs input-bordered input-primary"
                v-model="idStudent.val"
                @blur="clearValidity('idStudent')"
              />
            </div>

            <div class="form-control w-full max-w-xs">
              <label class="label">
                <span class="label-text">name : </span>
              </label>
              <input
                type="text"
                placeholder="Type here"
                class="input input-bordered w-full max-w-xs input-primary"
                v-model="name.val"
                @blur="clearValidity('name')"
              />
            </div>

            <div class="form-control w-full max-w-xs">
              <label class="label">
                <span class="label-text">sec : </span>
              </label>
              <input
                type="number"
                placeholder="Type here"
                class="input w-full max-w-xs input-bordered input-primary"
                v-model="sec.val"
                @blur="clearValidity('sec')"
              />
            </div>

            <br />
            <label
              for="my-modal-6"
              class="btn modal-button btn-primary"
              @click="saveData"
            >
              finished!
            </label>
          </div>
        </form>
      </body>
    </layout-telephone>
  </div>
</template>

<script>
import "@/assets/tailwind.css";
import LayoutTelephone from "../ui/LayoutTelephone.vue";
// import xhttp from 'xhttp';
import axios from "axios";
import Swal from "sweetalert2/dist/sweetalert2.js";
import Spinner from "../ui/Spinner.vue";
export default {
  components: { LayoutTelephone, Spinner },
  data() {
    return {
      idStudent: {
        isValid: true,
        val: 64160000,
      },
      name: {
        isValid: true,
        val: "",
      },
      sec: {
        isValid: true,
        val: 1,
      },

      formIsValid: true,
      Students: [],
      num1: 0,
      num2: 0,
      isLoad:false,
    };
  },
  created() {
    this.isLoad = true;
    let apiURL = "http://localhost:4000/api" || process.env.PORT;
    axios
      .get(apiURL)
      .then((res) => {
        this.Students = res.data;
        // console.log(this.Students);
      })
      .catch((error) => {
        console.log(error);
      });
    this.isLoad = false;

  },
  methods: {
    validForm() {
      this.formIsValid = true;

      for (let i = 0; i < this.Students.length; i++) {
        if (this.Students[i].section === 1) {
          this.num1++;
        }
        if (this.Students[i].section === 2) {
          this.num2++;
        }
        if (this.num1 > 40 || this.num2 > 40) {
          this.formIsValid = false;
          Swal.fire({
            icon: "error",
            title: "Oops...",
            text: "thisSection is full",
          });
        }
        if (this.Students[i].id === parseInt(this.idStudent.val)) {
          this.formIsValid = false;
          Swal.fire({
            icon: "error",
            title: "Oops...",
            text: "This Id is already exits!",
          });
          this.ClearForm();
          this.$router.replace("/form");
        }
      }
      if (this.idStudent.val < 64000000 && this.idStudent.val > 65000000) {
        this.formIsValid = false;
        Swal.fire({
          icon: "error",
          title: "Oops...",
          text: "Your Id wrong!",
        });
        this.$router.replace("/form");
      }
      if (this.name.val === "") {
        this.formIsValid = false;
        Swal.fire({
          icon: "error",
          title: "Oops...",
          text: "Your name must more than 3!",
        });
        this.$router.replace("/form");
      }
      if (this.sec.val < 1 || this.sec.val > 3) {
        this.formIsValid = false;
        Swal.fire({
          icon: "error",
          title: "Oops...",
          text: "Your sec wrong!",
        });
        this.$router.replace("/form");
      }
    },
    async saveData() {
      this.validForm();
      if (!this.formIsValid) {
        return;
      }

      let apiURL = "http://localhost:4000/api/create-student";
      axios
        .post(apiURL, {
          id: parseInt(this.idStudent.val),
          name: this.name.val,
          section: this.sec.val,
        })
        .then(() => {
          this.ClearForm();
        })
        .catch((error) => {
          console.log(error);
        });
      await Swal.fire("Good job!", "You clicked the button!", "success");
      await this.$router.replace("/formList");
    },
    ClearForm() {
      this.name = "";
      this.idStudent = "";
      this.sec = 1;
    },
    clearValidity(input) {
      this[input].isValid = true;
    },
  },
};
</script>

<style scoped>
.imgJelly {
  width: 225px;
  text-align: center;
}
</style>
