<template>
    <Spinner v-if="isLoad"></Spinner>

        <div class="body"  v-if="!isLoad">

            <br />
            <div class="stats bg-primary text-primary-content">
      
                <div class="stat">
                  <div class="stat-title">Section 1</div>
                  <div  class="stat-value">{{num1}}/40</div>
                  <div class="stat-actions">
                   
                  </div>
                </div>
                
                <div class="stat">
                  <div class="stat-title">Section 2</div>
                  <div class="stat-value">{{num2}}/40</div>
                  <div class="stat-actions">
                  </div>
                </div>
                
              </div>
            <div class="flex flex-col w-full ">
                <div class="grid h-50 card  rounded-box place-items-center">
                    <h1 class="text-3xl m-2 font-black text-blue-100">&#9997; Section 1 &#9997;</h1>
                    <img class="mx-6 w-4/5" id="imgCanZoom" src="@/assets/sec1.jpg" /><br /><br />
                    <TableSectionOne></TableSectionOne>
                </div>
                <div class="divider mx-6"></div>
                <div class="grid h-50 card  rounded-box place-items-center mb-2">
                    <h1 class="text-3xl m-2 font-black text-blue-100"> &#9997; Section 2 &#9997;</h1>
    
                    <img class="mx-6 w-4/5" src="@/assets/sec2.jpg" /><br /><br />
                    <TableSectionTwo></TableSectionTwo>
                </div>
            </div>
        </div>

    
</template>

<script>
// import './assets/tailwind.css'
import axios from "axios";
import NavBar from "@/components/ui/navBar.vue";
import TableSectionOne from "@/components/TableSectionOne.vue";
import TableSectionTwo from "@/components/TableSectionTwo.vue";
import Spinner from "@/components/ui/Spinner.vue";
export default {
    data() {
        return {
            isLoad: false,
            Students: [],
            num1: 0,
            num2: 0,


        }
    },

    components: { NavBar, TableSectionOne, TableSectionTwo, Spinner, Spinner },
    created() {
        this.isLoad = true;
        let apiURL =     process.env.PORT+"/api";
;
        axios.get(apiURL).then(res => {
            this.Students = res.data;
            this.Students.sort();
            console.log("Students.length : ")

            console.log(this.Students.length);
                        for(let i = 0;i<this.Students.length;i++){
                // console.log(this.Students[i].section);
                if(this.Students[i].section === 1){
                    this.num1++;
                }
                if(this.Students[i].section === 2){
                    this.num2++;
                }
            }
            return this.num1,this.num2
            
        }).catch(error => {
            console.log(error);
        });
        this.isLoad = false;
    
    }
   

};
</script>

<style scoped>
.body {
    background-color: #050505;
    opacity: 1;
    background-image: radial-gradient(#5f618c 0.75px, #050505 0.75px);
    background-size: 15px 15px;
}

</style>
